<script>
import mixin from '../mixin'

export default {
  mixins: [mixin],
  name: 'MobileMenu',
  props: {
    show: Boolean
  },
  data(){
    return {
      menu: null
    }
  },
  beforeMount() {
    this.menu = this.$store.state.data.menu;
  },
  methods: {
    clickOps(op) {
      this.$emit('toggleMenu', !this.show);
      setTimeout(() => {
        this.$emit('gotoPlace', op, this.$route.name === 'blog');
      }, 600);
    },
    clicklang() {
      let lang = this.$store.state.lang === 'en'? 'pt' : 'en';
      this.$store.commit('setlang', lang);
    },
    gohome() {
      this.$emit('toggleMenu', !this.show);
      this.$router.push('/');
    },

  }
}
</script>
