<script>
import mixin from '../mixin'

export default {
  mixins: [mixin],
  props:{ opened: Boolean },
  data(){
    return {
      menu: null
    }
  },
  beforeMount() {
    this.menu = this.$store.state.data.menu;
  },
  methods: {
    gohome() {
      this.$router.push('/');
    },
    clickOps(op) {
      this.$emit('gotoPlace', op, this.$route.name === 'blog')
    },
    clicklang() {
      let lang = this.$store.state.lang === 'en'? 'pt' : 'en';
      this.$store.commit('setlang', lang);
    },
    openCloseClick() {
      this.$emit('toggleMenu', !this.opened);
    }
  }
}
</script>
