<template>
  <div id="app">
    <div v-if="loaded">
      <router-view/>
    </div>
    <div v-else>
      <PreloaderPr/>
    </div>
  </div>
</template>
<script>
import PreloaderPr from './components/PreloaderPr.vue';

export default {
  components: {
    PreloaderPr,
  },
  data() {
    return {
      loaded: false
    }
  },
  beforeMount() {
    this.$http.get('pr.json')
      .then((result) => {
        setTimeout(() => {
          this.loaded = true;
          this.$store.commit('setdata', result.data);
        }, 2000);
      })
  }
  
}
</script>

<style lang="scss">
body {
  margin: 0;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
}
</style>
